/**
 * Selection.js
 * Selection with dynamic content
 *
 * @version 0.0.1
 * @author [Jia Hao](jiahao@policypal.com)
 */
import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Field, getFormValues } from 'redux-form';
import { Row, Col } from 'reactstrap';
import _ from 'lodash';

import CheckIcon from 'assets/images/common/CheckIcon.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import './Selection.scss';

const marginTopOffset = 10;

/**
 * Custom Selection Class
 */
class Selection extends PureComponent {
  static getStoreSelection(formStore, name) {
    const storeValues = formStore[name];
    if (!storeValues) {
      return null;
    }

    const storeValuesArray = storeValues.constructor === Array ? storeValues : [storeValues];
    const resultState = {};
    _.forEach(storeValuesArray, (v) => {
      if (v) { resultState[v] = true; }
    });
    return resultState;
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { resultState, selections } = prevState;
    const { formStore, name } = nextProps;
    if (formStore && formStore[name] !== resultState) {
      return {
        resultState: formStore[name],
        selections: Selection.getStoreSelection(formStore, name) || selections,
      };
    }

    return null;
  }
  constructor(props) {
    super(props);

    this.state = {
      selections: {},
    };
  }

  componentDidMount() {
    const {
      name,
      formStore,
      noEmptyTextSelection,
    } = this.props;

    if (formStore && formStore[name]) {
      this.setState({
        selections: Selection.getStoreSelection(formStore, name),
      });

      return;
    }

    if (noEmptyTextSelection) {
      this.setInitialValue();
    }
  }

  componentDidUpdate(prevProps) {
    const { name, formStore } = this.props;
    if (name !== prevProps.name) {
      const value = formStore[name];
      if (!value) {
        this.setInitialValue();
      }
    }
  }

  onToggleSelection(option) {
    const { selections } = this.state;
    const {
      name,
      type,
      change,
      noEmptyTextSelection,
    } = this.props;

    const { key } = option;

    const toggleResult = !this.state.selections[key];

    let newSelections = {};
    let resultState = null;
    let noUpdate = false;
    switch (type) {
      case 'text-selection':
      case 'radio':
        if (!noEmptyTextSelection || (noEmptyTextSelection && !selections[key])) {
          newSelections = _.mapValues(selections, () => false);
          newSelections[key] = toggleResult;
          resultState = _.findKey(newSelections, v => v);
        } else {
          noUpdate = true;
        }
        break;
      case 'checkbox':
        newSelections = Object.assign({}, selections, { [key]: toggleResult });
        resultState = [];
        _.each(newSelections, (v, k) => {
          if (v) { resultState.push(k); }
        });
        break;
      default: break;
    }

    if (!noUpdate) {
      this.setState({ selections: newSelections });
      change(name, resultState);
    }
  }

  setInitialValue() {
    const {
      options,
      change,
      name,
    } = this.props;

    const newSelections = { [options[0].key]: true };
    this.setState({
      selections: newSelections,
    });
    change(name, _.findKey(newSelections, v => v));
  }

  renderSelectionInsides(option, selectionState) {
    const { type } = this.props;

    switch (type) {
      case 'radio':
        return <div className={`${type}-inner-${selectionState}`} />;
      case 'checkbox':
        return <img src={CheckIcon} className={`checkbox-inner-${selectionState}`} alt="check" />;
      case 'text-selection':
        return <div className="description py-3">{option.description}</div>;
      default:
        return null;
    }
  }

  renderSelectionIcon(option) {
    const { selections } = this.state;
    const { type, sizeInSM } = this.props;
    const { key, description } = option;
    const isSelected = (selections[key] || false);
    const selectionState = (isSelected) ? 'highlighted' : 'default';

    const children = (
      <div
        role="button"
        onKeyPress={() => {}}
        onClick={() => this.onToggleSelection(option)}
        tabIndex={0}
        className={`${type}-outer-${selectionState}`}
      >
        {this.renderSelectionInsides(option, selectionState)}
      </div>
    );

    if (type === 'text-selection') {
      return (
        <Col sm={sizeInSM} key={key} className="text-sel-col">
          <div className="single-selection" key={key}>
            {children}
          </div>
        </Col>
      );
    }

    return (
      <Col sm={sizeInSM} key={key}>
        <div className="single-selection">
          {children}
          <div className="description">{description}</div>
        </div>
      </Col>
    );
  }

  render() {
    const {
      name,
      options,
      type,
      desc,
      extraInfo,
      errorString,
    } = this.props;

    return (
      <FormInputWrapper
        desc={desc}
        ref={node => (this.formParent = node)}
        extraInfo={
          <div style={{ marginTop: -marginTopOffset }}>
            {extraInfo}
          </div>
        }
        errorString={errorString}
      >
        <Field name={name} component={() => <div />} />
        <div className="selection">
          <Row className={(type === 'text-selection') ? 'text-sel-container' : ''}>
            {
              _.map(options, option => this.renderSelectionIcon(option))
            }
          </Row>
        </div>
      </FormInputWrapper>
    );
  }
}

Selection.propTypes = {
  name: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  options: PropTypes.array.isRequired, // each option is { key, description }
  change: PropTypes.func.isRequired,
  sizeInSM: PropTypes.number,
  desc: PropTypes.string,
  formStore: PropTypes.object,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  noEmptyTextSelection: PropTypes.bool,
};

Selection.defaultProps = {
  sizeInSM: 12,
  desc: null,
  formStore: null,
  extraInfo: null,
  errorString: null,
  noEmptyTextSelection: false,
};

const mapStateToProps = (state, ownProps) => ({
  formStore: (getFormValues(ownProps.form)(state)),
});

export default connect(mapStateToProps, null)(Selection);
