/**
 * RestrictionPopup.js
 * Used to display product restrictions
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Row, Col, Modal, ModalHeader } from 'reactstrap';
import Button from 'components/FormsV3/Button/Button';

import { clearRestrictionPopup } from '../../../actions/restrictionsActions';

import './RestrictionsPopup.scss';

class RestrictionsPopup extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { data } = nextProps;

    if (data && data !== prevState.data) {
      nextProps.clearRestrictionPopup();
      return ({
        values: data.values,
        clickCallback: data.clickCallback,
        doAcceptTag: data.doAcceptTag,
      });
    }

    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      values: null,
      clickCallback: null,
      doAcceptTag: null,
      modal: false,
    };

    this.setData = this.setData.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidUpdate() {
    const { values, modal } = this.state;

    if (values && !modal) {
      this.setState({ modal: true });
    }
  }

  setData(values, clickCallback, doAcceptTag) {
    this.setState({
      values,
      clickCallback,
      doAcceptTag,
    });

    setTimeout(() => this.setState({ modal: true }), 100);
  }

  handleClick() {
    const { clickCallback, doAcceptTag } = this.state;

    if (doAcceptTag) {
      doAcceptTag();
    }

    if (clickCallback) {
      clickCallback();
    }

    this.setState({ modal: false, values: null });
  }

  renderData() {
    const { values } = this.state;

    return _.map(values, (item, index) => {
      const description = _.split(item.description, '\n');
      return (
        <Fragment key={`res-data-${index}`}>
          <Col className="title" xs={12} sm={4}>
            <p>{item.title}</p>
          </Col>
          <Col className="res-desc" xs={12} sm={8}>
            {
              _.map(description, (desc, descIndex) => (
                <p key={`res-desc-${descIndex}`}>{desc}</p>
              ))
            }
          </Col>
          <Col xs={12}>
            <div className="line" xs={12} />
          </Col>
        </Fragment>
      );
    });
  }

  render() {
    const { modal } = this.state;
    return (
      <Modal
        backdropClassName="blue-modal-backdrop"
        className="restrictions-popup"
        isOpen={modal}
        size="lg"
      >
        <ModalHeader toggle={() => this.setState({ modal: false, values: null })} />
        <Row>
          <Col className="header" xs={12}>
            <p>{'Company\'s Restrictions'}</p>
            <p className="sub">Scroll down to read the restrictions</p>
          </Col>
          {this.renderData()}
        </Row>
        <Row>
          <Col className="button-container" xs={12}>
            <Button
              onClick={this.handleClick}
              type="submit"
              variant="medium-flat-green"
              width={165}
              height={72}
            >
              <p>Accept</p>
            </Button>
          </Col>
        </Row>
      </Modal>
    );
  }
}

const mapStateToProps = state => ({
  data: state.restrictions.data,
});

const mapDispatchToProps = dispatch => ({
  clearRestrictionPopup: () => {
    dispatch(clearRestrictionPopup());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(RestrictionsPopup);
